import { history } from "../../App"
import { quanLyPhimService } from "../../services/QuanLyPhimService"
import { notifiFunction } from "../../ultil/Notification/notification"
import { SET_DANH_SACH_PHIM, SET_THONG_TIN_PHIM } from "./types/QuanLyPhimType"


export const layDanhSachPhimAction = (tenPhim = '') => {
    return async (dispatch) => {
        try {
            const result = await quanLyPhimService.layDanhSachPhim(tenPhim)
            //sau khi ly d liu t api v => redux (reducer)
            dispatch({
                type: SET_DANH_SACH_PHIM,
                arrFilm: result.data.content
            })
        } catch (errors) {
            console.log('errors', errors)
        }
    }
}

export const themPhimUpLoadHinhAction = (formData) => {
    return async (dispatch) => {
        try {
            let result = await quanLyPhimService.themPhimUploadHinh(formData)
            notifiFunction('success', 'Thm phim mi thnh cng!')
            console.log('result', result.data.content)
        } catch (err) {
            console.log(err)
            console.log(err.response?.data)
            console.log(err.response)
            notifiFunction('error', 'Thm phim tht bi!')
        }
    }
}

export const capNhatPhimUpLoadAction = (formData) => {
    return async (dispatch) => {
        try {
            let result = await quanLyPhimService.capNhatPhimUpload(formData)
            history.push('/admin/films')
            dispatch(layDanhSachPhimAction())
            notifiFunction('success', 'Ct nht thng tin thnh cng!')
        } catch (err) {
            console.log(err.response?.data)
            notifiFunction('error', 'Ct nht thng tin tht bi! Bn vui lng th li.')
        }
    }
}

export const layThongTinPhimAction = (maPhim) => {
    return async (dispatch) => {
        try {
            let result = await quanLyPhimService.layThongTinPhim(maPhim)
            dispatch({
                type: SET_THONG_TIN_PHIM,
                thongTinPhim: result.data.content
            })
        } catch (err) {
            console.log(err.response?.data)

        }
    }
}

export const xoaPhimAction = (maPhim) => {
    return async (dispatch) => {
        try {
            let result = await quanLyPhimService.xoaPhim(maPhim)
            notifiFunction('success', 'Xa phim thnh cng!')
            //sau khi xa load li danh sch 
            dispatch(layDanhSachPhimAction())

        } catch (err) {
            notifiFunction('error', 'Xa phim tht bi')
            console.log(err.response?.data)
        }
    }
}

